.campo {
  margin: 24px 0;
}

.campo label {
  display: block;
  margin-bottom: 8px;
  font-size: 24px;
}

.campo input {
  background: #ffffff;
  box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
  border-radius: 0px;
  width: 100%;
  border: none;
  font-size: 24px;
  padding: 24px;
  box-sizing: border-box;
}
.campo-color input {
  width: 10%;
  padding: 0;
  cursor: pointer;
}
